@import url(https://fonts.googleapis.com/css?family=Quicksand:300,500);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css);
@import url(https://fonts.googleapis.com/css?family=Quicksand:300,500);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.carousel-button {
  border-radius: 50%;
}

.carousel-button:hover {
  background-color: #ddd;
  color: black;
}

#carousel {
  width: 500px;
}

html,
body {
  height: 100%;
  position: relative;
}

.intro {
  display: inline-flex;
  width: auto;
  height: auto;
  margin: 0 auto;
  /* padding-top: 25%; */
}
.intro h1 {
  z-index: 0;
  font-family: "Quicksand", sans-serif;
  font-size: 5vw;
  color: #616161;
  overflow: hidden; /* Ensures the content is not revealed until the animation */
  border-right: 0.15em solid #14b8af; /* The typwriter cursor */
  white-space: nowrap; /* Keeps the content on a single line */
  margin: 0 auto; /* Gives that scrolling effect as the typing happens */
  letter-spacing: 0.15em; /* Adjust as needed */
  -webkit-animation: typing 3.5s steps(40, end), blink-caret 0.75s step-end infinite;
          animation: typing 3.5s steps(40, end), blink-caret 0.75s step-end infinite;
}

.color {
  background-color: #14b8af;
}
.words {
  font-family: "Quicksand", sans-serif;
  font-size: 3vw;
  color: white;
  word-wrap: break-word;
  width: 600px;
  /* min-width: 45vw; */
  display: flex;
  justify-content: center;
  align-self: center;
  align-content: center;
  padding: 3vw;
}

.wordColorSize {
  font-family: "Quicksand", sans-serif;
  font-size: 3.5vw;
  color: white;
  width: 47vw;
}

.landing {
  align-items: center;
  justify-content: space-around;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  min-height: 85vh;
  width: 100vw;
}

.headshot {
  float: left;
  padding: 50px;
}
.headshot img {
  border-radius: 3vw;
  border: 4px solid white;
  box-shadow: 0 4px 16px #616161;
}

.view-resume {
  background-color: #616161;
  font-family: "Quicksand", sans-serif;
  border: none;
  border-radius: 8px;
  color: white;
  padding: 15px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 30px;
  margin: 15px 2px 30px;
  cursor: pointer;
}

.card {
  display: flex;
  justify-content: center;
  align-content: center;
  flex-flow: row wrap;
  max-width: 420px;
  min-height: 20vh;
  box-shadow: 0 4px 16px #616161;
  border-radius: 40px;
  word-wrap: break-word;
  line-height: 20px;
  margin: 15px;
  float: top;
}
.card p {
  width: 25vw;
  color: #616161;
}

.card h3 {
  color: #14b8af;
  width: 100%;
}

.card h1 {
  width: 100%;
}

.card-container {
  display: flex;
  align-content: center;
  flex-flow: row wrap;
  justify-content: space-around;
  margin: 30px;
}

.homeSection {
  display: flex;
  align-content: center;
  justify-content: space-around;
  flex-flow: row wrap;
  width: 100vw;
  min-height: 100vh;
  font-family: "Quicksand", sans-serif;
}

.homeSection.color {
  background-color: #14b8af;
}

.homeSection.bg {
  background-color: white;
  overflow: hidden;
  display: inline-block;
  padding: 10px;
  font-weight: bold;
  color: #14b8af;
  font-size: 9.5vw;
  font-family: "Quicksand", sans-serif;
}
.homeSection.bg-text {
  background-image: url(/marjan-personal-website/static/media/t2.1b42f922.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.rotateLeft {
  -webkit-transform: rotate(5deg);
          transform: rotate(5deg);
}

.rotateRight {
  -webkit-transform: rotate(-5deg);
          transform: rotate(-5deg);
}

.fade-in-section {
  opacity: 0;
  -webkit-transform: translateY(20vh);
          transform: translateY(20vh);
  visibility: hidden;
  -webkit-transition: opacity 0.6s ease-out, -webkit-transform 1.2s ease-out;
  transition: opacity 0.6s ease-out, -webkit-transform 1.2s ease-out;
  transition: opacity 0.6s ease-out, transform 1.2s ease-out;
  transition: opacity 0.6s ease-out, transform 1.2s ease-out, -webkit-transform 1.2s ease-out;
  will-change: opacity, visibility;
}
.fade-in-section.is-visible {
  opacity: 1;
  visibility: visible;
  -webkit-transform: none;
          transform: none;
}
.footer {
  position: relative;
  bottom: 0;
  width: 100%;
  background-color: #14b8af;
  font-size: 50px;
  bottom: 0;
  height: 20vh;
}

i {
  color: white;
  padding: 50px;
}

i.fas {
  color: white;
}

i.fas.fa-facebook-official {
  color: white;
}

i.fas.fa-github {
  color: white;
}
i.fas.fa-linkedin {
  color: white;
}

@-webkit-keyframes movingTopToBottom {
  0% {
    top: -500px;
  }
  100% {
    top: 5px;
    opacity: 100;
  }
}

@keyframes movingTopToBottom {
  0% {
    top: -500px;
  }
  100% {
    top: 5px;
    opacity: 100;
  }
}

/* The typing effect */
@-webkit-keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}
@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

/* The typewriter cursor effect */
@-webkit-keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: #14b8af;
  }
}
@keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: #14b8af;
  }
}

#nav-container {
  width: 100%;
  background: transparent;
  float: right;
  z-index: 1;
}
nav {
  height: 30px;
  padding: 0;
  margin: 0;
}
nav ul {
  top: 0;
  float: right;
  list-style-type: none;
  margin: 0;
  text-align: center;
  display: flex;
  flex-flow: row wrap;
  align-content: flex-start;
  justify-content: center;
  align-items: center;
  background: transparent;
  -webkit-transition: top 0.3s;
  transition: top 0.3s;
}

nav li {
  float: top;
  margin: 25px;
  padding-right: 20px;
}
nav li a {
  display: inline-block;
  color: #14b8af;
  font-family: "Quicksand", sans-serif;
  font-weight: 1000;
  margin: 10%;
  padding: 10px;
  font-size: 20px;
  text-decoration: none;
  text-align: center;
  align-content: center;
}
nav li a:hover {
  animation: bounce 2.5s infinite;
  -webkit-animation: bounce 2.5s infinite;
  -moz-animation: bounce 2.5s infinite;
  -o-animation: bounce 2.5s infinite;
}

@-webkit-keyframes bounce {
  0%,
  100% {
    -webkit-transform: translateY(0);
  }
  50% {
    -webkit-transform: translateY(-10px);
  }
}
@keyframes bounce {
  0%,
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  50% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
}

.App {
  text-align: center;
  overflow: hidden;
  bottom: 0;
  margin: 0 auto;
  min-height: 100vh;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

